document.addEventListener('DOMContentLoaded', function() {
    const modalContainer = document.createElement('div');
    // document.body.appendChild(modalContainer);

    // Modal HTML dosyasƒ±nƒ± y√ºkle
    fetch('modal.html')
        .then(response => response.text())
        .then(html => {
            modalContainer.innerHTML = html;

            const modal = document.getElementById('teamModal'); //Modal ID'si deƒüi≈üecek
            const teamLink = document.getElementById('teamLink');
            const closeButton = document.getElementById('closeModal');

            // Modal a√ßma i≈ülevi
            teamLink.addEventListener('click', function() {
                modal.classList.add('active');
                document.body.style.overflow = 'hidden';
            });

            // Modal kapatma i≈ülevi
            function closeModal() {
                modal.classList.remove('active');
                document.body.style.overflow = 'auto';
            }

            // X butonuna tƒ±klama i≈ülevi
            closeButton.addEventListener('click', closeModal);

            // Modal dƒ±≈üƒ±na tƒ±klama i≈ülevi
            modal.addEventListener('click', function(e) {
                if (e.target === modal) {
                    closeModal();
                }
            });

            // ESC tu≈üuyla modal kapatma i≈ülevi
            document.addEventListener('keydown', function(e) {
                if (e.key === 'Escape') {
                    closeModal();
                }
            });
        })
        .catch(error => console.error('Modal y√ºklenirken bir hata olu≈ütu:', error));
});